  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

:root {
  font-size: 62.5%;
}

@font-face {
  font-family: 'Nabla';
  src: url(${___CSS_LOADER_URL_REPLACEMENT_0___})
    format('truetype');
}

@font-face {
  font-family: 'Joystix';
  src: url(${___CSS_LOADER_URL_REPLACEMENT_1___}) format('opentype');
}

body {
  font-size: 1.6rem;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  background:
    linear-gradient(rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.4)),
    url(${___CSS_LOADER_URL_REPLACEMENT_2___}) no-repeat center center;
  background-size: cover;
}
dialog {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 45rem;
  height: 30rem;
  border-radius: 1rem;
  background-color: aquamarine;
  font-family: 'Joystix';
  color: rgb(232, 172, 20);
  text-align: center;
  text-shadow: 2px 2px rgb(118, 118, 118);
  font-size: 2.6rem;
  > form {
    width: 100%;
    height: 100%;
    padding: 1rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 1rem;
  }
}


label {
  font-size: 2.5rem;
}

h2,
span {
  font-family: 'Joystix';
  color: rgb(232, 172, 20);
  text-align: center;
  text-shadow: 2px 2px rgb(118, 118, 118);
  font-size: 3rem;
}

span {
  border: 2px ridge black;
  border-radius: 0.3rem;
  padding: 0.5rem;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: aliceblue;
}

p {
  font-size: 3rem;
  letter-spacing: 0.3rem;
}

input[type='text'] {
  width: 20rem;
  height: 3rem;
  font-family: monospace;
  padding: 0.5rem;
}

button {
  width: fit-content;
  height: fit-content;
  padding: 0.7rem;
  border-radius: 1rem;
  border: none;
  font-size: 1.6rem;
  cursor: pointer;
  background-color: #00bcd4;
  box-shadow: 1px 1px 3px black;
  color: rgb(232, 172, 20);
  text-align: center;
  text-shadow: 2px 2px rgb(118, 118, 118);
  font-size: 2rem;
  font-family: 'Joystix';
}

kbd {
  padding: 5px 8px;
  font-family: Arial, sans-serif;
  font-size: 0.85em;
  background-color: #f7f7f7;
  border: 1px solid #ccc;
  border-bottom-color: #bbb;
  box-shadow:
    0 1px 0 #fff,
    0 2px 0 #bbb;
  border-radius: 3px;
  color: #333;
  text-shadow: 0 1px 0 #fff;
}

#pass-device-dialog {
  width: 100%;
  height: 100%;
  > form {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
}
#title {
  margin-top: 2rem;
  width: 100%;
  height: 10%;
  text-align: center;
  background: url(${___CSS_LOADER_URL_REPLACEMENT_3___}) no-repeat center center;
  background-size: contain;
}

.message-box {
  width: 90%;
  height: 100%;
  background-color: rgba(240, 248, 255, 0.72);
  border-radius: 5px;
  font-size: 1.5rem;
  font-family: 'Joystix';
  color: rgb(232, 172, 20);
  text-align: center;
  text-shadow: 2px 2px rgb(118, 118, 118);
  font-size: 1.5rem;
  display: flex;
  align-items: center;
  justify-content: center;
}

#gameboards {
  display: flex;
  width: 100%;
  height: 90%;
  padding: 1rem;
}

.gameboard-divs {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1rem;
  width: 50%;
  height: 100%;
}

.board-and-deck-container {
  width: 100%;
  height: 75%;
  display: flex;
  gap: 1.5rem;
}

.fleet-deck {
  align-self: flex-end;
  width: 20%;
  height: 90%;
  display: grid;
  position: relative;
}

#player1fleet {
  grid:
    'carrier1 submarine1' 1fr
    'carrier1 submarine1' 1fr
    'carrier1 submarine1' 1fr
    'carrier1 destroyer1' 1fr
    'carrier1 destroyer1' 1fr
    'battleship1 .' 1fr
    'battleship1 cruiser1' 1fr
    'battleship1 cruiser1' 1fr
    'battleship1 cruiser1' 1fr
    / 1fr 1fr;
}

#player2fleet {
  grid:
    'carrier2 submarine2' 1fr
    'carrier2 submarine2' 1fr
    'carrier2 submarine2' 1fr
    'carrier2 destroyer2' 1fr
    'carrier2 destroyer2' 1fr
    'battleship2 .' 1fr
    'battleship2 cruiser2' 1fr
    'battleship2 cruiser2' 1fr
    'battleship2 cruiser2' 1fr
    / 1fr 1fr;
}

.ship {
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
  width: 100%;
  height: 100%;
  z-index: 2;
}

.ship.vertical {
  transform: rotate(0deg);
}

.ship.horizontal {
  transform: rotate(90deg);
}

#carrier1 {
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_4___});
  grid-area: carrier1;
}

#battleship1 {
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_5___});
  grid-area: battleship1;
}

#submarine1 {
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_6___});
  grid-area: submarine1;
}

#destroyer1 {
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_7___});
  grid-area: destroyer1;
}

#cruiser1 {
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_8___});
  grid-area: cruiser1;
}

#carrier2 {
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_4___});
  grid-area: carrier2;
}

#battleship2 {
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_5___});
  grid-area: battleship2;
}

#submarine2 {
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_6___});
  grid-area: submarine2;
}

#destroyer2 {
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_7___});
  grid-area: destroyer2;
}

#cruiser2 {
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_8___});
  grid-area: cruiser2;
}

.gameboard-grid {
  display: grid;
  grid-template: repeat(11, minmax(25px, 1fr)) / repeat(11, minmax(25px, 1fr));
  width: 80%;
  height: 100%;
  background-color: #00bcd4;
  }

.gameboard-grid .label {
  background-color: rgba(255, 255, 255, 0.572);
}


.label {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: 'Nabla';
  font-size: 3.3rem;
  text-shadow: 1px 1px rgb(118, 118, 118);
}

.tile {
  width: 100%;
  height: 100%;
  border: 1px solid black;
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_9___});
  background-color: #00bcd4;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
  color: white;
  font-size: 3rem;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  position: relative;
}
.tile.hit {
  background-color: transparent;
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_10___});
  z-index: 5;
}

.alternate-color .tile.hit {
  background-color: #00bcd4;
}

/* Pseudo-elements for the lines of the 'X' */
.tile::before,
.tile::after {
  display: none;
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  width: 110%;
  height: 2px;
  background-color: red;
  transform-origin: center;
  z-index: 5;
}

.tile::before {
  transform: translate(-50%, -50%) rotate(45deg);
}

.tile::after {
  transform: translate(-50%, -50%) rotate(-45deg);
}

.tile.missed::before {
  display: block;
  animation: scaleDown 0.3s ease-out;
}

.tile.missed::after {
  display: block;
  animation: scaleDownReverse 0.3s ease-out;
}

@keyframes scaleDown {
  from {
    transform: scale(1.5) translate(-35%, -25%) rotate(45deg);
  }
  to {
    transform: scale(1) translate(-50%, -50%) rotate(45deg);
  }
}

@keyframes scaleDownReverse {
  from {
    transform: scale(1.5) translate(-35%, -25%) rotate(-45deg);
  }
  to {
    transform: scale(1) translate(-50%, -50%) rotate(-45deg);
  }
}
